import React, { useState, useEffect } from 'react';
import { Card, CardBody, CardHeader, Col, Row, Table } from 'reactstrap';
import { getTeacher } from '../../handlers/Api/Calls';
import { ErrorToast, Loading } from "../../components";

const Teacher = props => {
  const [data, setData] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    getTeacher(props.match.params.id , 
      (apiLoading, apiData, apiError) => {
        setData(apiData);
        setError(apiError);
        setIsLoading(apiLoading);
      }
    );
  }, [props.match.params.id]); 

  const teacherDetails = data ? Object.entries(data) : [['id', (<span><i className="text-muted icon-ban"></i> Not found</span>)]]

  return (
    <div className="animated fadeIn">
      {error && <ErrorToast error={error} />}
      <Row>
        <Col lg={6}>
          <Card>
            <CardHeader>
              <strong><i className="icon-info pr-1"></i>Teacher id: {props.match.params.id}</strong>
            </CardHeader>
            <CardBody>
              <Table responsive striped hover>
                <tbody>
                  { isLoading ? <Loading /> :
                    teacherDetails.map(([key, value]) => {
                      return (
                        <tr key={key}>
                          <td>{`${key}:`}</td>
                          <td><strong>{value}</strong></td>
                        </tr>
                      )
                    })
                  }
                </tbody>
              </Table>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  )
}

export default Teacher;
